const Precio = (props) => {

    const {PRICE, CHANGEPCTDAY, CHANGEPCT24HOUR } = props.precio;
    
    return ( 
        <div className="card text-white bg-success mb-3">
            <div className="card-header">Precio del Bitcoin</div>
            <div className="card-body">
                <h4 className="card-title">Precio Actual: $ {PRICE} </h4>
                <div className="d-md-flex justify-content-between">
                        <p className="card-text">
                            <span className="font-weight-bold">Última Hora: </span> {CHANGEPCTDAY} %
                        </p> 
                        <p className="card-text">
                            <span className="font-weight-bold">Últimas 24 horas: </span> {CHANGEPCT24HOUR} %
                        </p> 
                </div>
            </div>
        </div>
    );
}

export default Precio;
